import { makeElement } from './template_generation_tools'
export const tabbedControls = function tabbedControls(name, options={}, selectedValue='') {
  const tabsContainer = makeElement('div', ['kubik-wysiwyg-tabs-container'])
  const tabsContent = makeElement('div', ['kubik-wysiwyg-tabs-content'])
  options.forEach( function(option, index) {
    const id = `${name}-${option.key}`
    const label = makeElement('label', 'kubik-wysiwyg-tab-label', {htmlFor: id})
    const opt = makeElement('input', 'kubik-wysiwyg-tab-input', { id: id, type: 'radio', value: option.key, name: name})

    if (selectedValue !== '') {
      opt.checked = option.key == selectedValue
    } else {
      opt.checked = index == 0;
    }

    let contents = option.contents

    if(typeof(option.contents) === 'undefined') {
      contents = makeElement('span')
    } else {
      option.contents.classList.add('kubik-wysiwyg-tab-content')
    }

    label.innerText = option.label
    tabsContainer.appendChild(opt)
    tabsContainer.appendChild(label)
    tabsContainer.appendChild(contents)
  })
  return tabsContainer
};
