import EditorJS from '@editorjs/editorjs'
import Header from '@editorjs/header';
import NestedList from '@editorjs/nested-list';
import Underline from '@editorjs/underline';
import Embed from '@editorjs/embed';
import Paragraph from '@editorjs/paragraph';
import Quote from '@editorjs/quote';
import KubikImage from '../plugins/kubik_image';
import Hyperlink from 'editorjs-hyperlink';
import { Controller } from "@hotwired/stimulus"

export default class extends Controller {
  static targets = ['editor', 'input']
  static values = { content: Object }

  connect() {
    this.fetchContents()
    this.initializeEditor()
  }

  initializeEditor() {
    this.editor = new EditorJS({
      holder: this.element,
      minHeight: 100,
      tools: {
        image: KubikImage,
        paragraph: {
          class: Paragraph,
          inlineToolbar: ['bold', 'underline', 'hyperlink', 'italic'],
        },
        hyperlink: {
          class: Hyperlink,
          config: {}
        },
        embed: {
          class: Embed,
          config: {
            services: {
              youtube: true,
              vimeo: true,
              twitter: true,
              instagram: true,
            }
          }
        },
        header: {
          class: Header,
          config: {
            placeholder: "Add header text",
            levels: [2, 3, 4, 5, 6],
            defaultLevel: 2
          }
        },
        nested_list: {
          class: NestedList,
          inlineToolbar: true
        },
        quote: {
          class: Quote,
          inlineToolbar: true,
          shortcut: 'CMD+SHIFT+O',
          config: {
            quotePlaceholder: 'Enter a quote',
            captionPlaceholder: 'Quote\'s author',
          },
        },
        underline: Underline,
      },
      data: this.contentValue,
      onChange: () => {
        this.saveContents()
      },
    });
  }

  saveContents() {
    this.editor
      .save()
      .then((savedData) => {
        this.inputTarget.value = JSON.stringify(savedData)
      })
      .catch((error) => {
        console.log('Saving failed: ', error)
      });
  }

  fetchContents() {
    try {
      this.contentValue = JSON.parse(this.inputTarget.value)
    } catch (e) {
      this.contentValue = {}
    }
  }
}
